import React, { Component } from 'react';
import { CSSTransition } from 'react-transition-group';
import '../styles/animation-s.scss';
import '../styles/tablet-animations.scss';
import Raster from '../assets/image/raster.png';
import kulturEurPic from '../assets/image/Kreativa_Europa_Kultur.png';

class ShowPinkPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blue: props.blue,
            green: props.green,
            pink: props.pink,
            showRects: true,
            welcome: props.welcome,
            counter: props.counter
        }
    }

    render() {
        let animate = <div></div>;
        let pinkText = <div>
        <div className="container static-page">
            <div className="row" id="text-row">
                <div className="col-12">
                    <div className="information-text">Delprogrammet Kultur vänder sig till aktörer inom musik, scenkonst, bildkonst, design, konsthantverk, arkitektur, litteratur, tidskrifter, radio, arkiv, bibliotek, museer, kulturarv och kulturmiljö. Stöd finns för europeiska samarbetsprojekt, nätverk och plattformar samt översättning och spridning av europeisk skönlitteratur. Aktörer inom den audiovisuella sektorn kan också delta
        i samarbetsprojekt.</div>
                </div>
            </div>
            <div className="row justify-content-around align-items-center">
                <div className="col-8">
                <i class="fas fa-chevron-right"></i><span>  </span><a href="https://www.kulturradet.se/kreativa-europa/" className="static-page-link">Kreativa Europa Kultur</a>
                </div>
                <div className="col-4">
                <a href="https://www.kulturradet.se/kreativa-europa/"><img src={kulturEurPic} className="icon-pic" alt="not loading"></img></a>
                </div>
            </div>
        </div>
        </div>

        /**
         * check if first time pressing a link.
         * If this is the case animate
         * from: split
         * to : center
         * -------
         * ELSE animate
         * from: center
         * to center
         */

        if (this.props.counter > 1) {
            animate = <div>
                <div className="raster raster-static-pink">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
                <div className="rect-anim static-pink-rect" id="pinkRect">
                    {pinkText}
                </div>
            </div>
        } else {
            animate = <div>
            <CSSTransition
                in={this.props.pink}
                timeout={0}
                classNames="green-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="rect-anim green" id="greenRect">
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.pink}
                timeout={0}
                classNames="green-raster-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="raster">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.pink}
                timeout={0}
                classNames="blue-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="rect-anim blue" id="blueRect">
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.pink}
                timeout={0}
                classNames="blue-raster-transition-under"
                unmountOnExit
                appear
                exit
            >
                <div className="raster">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.pink}
                timeout={0}
                classNames="pink-transition-ontop"
                unmountOnExit
                appear
                // This will create an animation from full color as in prev state to transparent
                //onEntered={()=> document.getElementById("abc").hidden = true}
                exit
            >
                <div className="rect-anim pink" id="pinkRect">

                    {pinkText}
                </div>
            </CSSTransition>
            <CSSTransition
                in={this.props.pink}
                timeout={0}
                classNames="pink-raster-transition-ontop"
                unmountOnExit
                appear
                exit
            >
                <div className="raster">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
            </CSSTransition>
        </div>
        }
        return (
            <div>
                {animate}
            </div>
        );
    }
}

export default ShowPinkPage;