import React, { Component } from 'react';
import { CSSTransition } from 'react-transition-group';
import '../styles/animation-s.scss';
import '../styles/tablet-animations.scss';
import Raster from '../assets/image/raster.png';

class ShowBluePage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blue: props.blue,
            green: props.green,
            pink: props.pink,
            showRects: true,
            welcome: props.welcome,
            counter: props.counter
        }
        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(event) {
    }
    render() {
        let animate = <div></div>;
        let blueText =
            <div>
                <div className="container static-page">
                    <div className="row" id="text-row">
                        <div className="col-12">
                            <div className="information-text">Kreativa Europa är Europeiska kommissionens program för de kulturella och kreativa sektorerna under 2014–2020. Programmet erbjuder möjligheter för aktörer inom tv, film, dataspel, konst och kultur att samarbeta internationellt och
        nå ut till en internationell publik.<br></br><br></br>
                                Kreativa Europa består av delprogrammen Kultur och MEDIA. Kreativa Europa Desk Sverige svarar på frågor om programmet och ger råd vid ansökan. Kulturrådet ansvarar för delprogrammet Kultur och Filminstitutet för delprogrammet MEDIA.</div>
                        </div>
                    </div>
                </div>
            </div>
        /**
         * check if first time pressing a link. 
         * If this is the case animate 
         * from: split
         * to : center
         * -------
         * ELSE animate
         * from: center
         * to center
         * and change background-color
         * from: full
         * to: transparent
         */
        if (this.props.counter > 1) {
            animate = <div>
                <div className="raster raster-static-blue">
                    <img src={Raster} width="100%" height="100%" alt="" ></img>
                </div>
                <div className="rect-anim static-blue-rect" id="blueRect">
                    
                    {blueText}
                </div>
            </div>
        } else {
            animate = <div>
                <CSSTransition
                    in={this.props.blue}
                    timeout={0}
                    classNames="green-transition-under"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim green" id="greenRect">
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.blue}
                    timeout={0}
                    classNames="green-raster-transition-under"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.blue}
                    timeout={0}
                    classNames="pink-transition-under"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim pink" id="pinkRect">
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.blue}
                    timeout={0}
                    classNames="pink-raster-transition-under"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.blue}
                    timeout={0}
                    classNames="blue-transition-ontop"
                    unmountOnExit
                    appear
                    // This will create an animation from full color as in prev state to transparent
                    //onEntered={()=> document.getElementById("abc").hidden = true}
                    exit
                >
                    <div className="rect-anim blue" id="blueRect">

                        {blueText}
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.blue}
                    timeout={0}
                    classNames="blue-raster-transition-ontop"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                </CSSTransition>
            </div>
        }
        return (
            <div>
                {animate}
            </div>
        );
    }
}

export default ShowBluePage;





