import React, { Component } from 'react';
import { CSSTransition } from 'react-transition-group';
import '../styles/animation-s.scss';
import '../styles/tablet-animations.scss';
import drom from '../assets/image/drom.png';
import dela from '../assets/image/dela.png';
import skapa from '../assets/image/skapa.png';
import Raster from '../assets/image/raster.png';

class WelcomeRects extends Component {

    render() {
        let dromImage, delaImage, skapaImage;
        dromImage =
            <div className="img-wrapper">
                <img src={drom} width="100%" height="100%" alt="img not loading"></img>
            </div>
        delaImage =
            <div className="img-wrapper">
                <img src={dela} width="100%" height="100%" alt="img not loading"></img>
            </div>
        skapaImage =
            <div className="img-wrapper">
                <img src={skapa} width="100%" height="100%" alt="img not loading"></img>
            </div>

        return (
            <div>
                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="green-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim green" id="greenRect" >
                        {delaImage}
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="green-raster-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster raster-static-green">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                </CSSTransition>

                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="pink-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim pink" id="pinkRect" >
                        {dromImage}
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="pink-raster-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster raster-static-pink">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                </CSSTransition>

                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="blue-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="rect-anim blue" id="blueRect" >
                        {skapaImage}
                    </div>
                </CSSTransition>
                <CSSTransition
                    in={this.props.welcome}
                    timeout={700}
                    classNames="blue-raster-transition"
                    unmountOnExit
                    appear
                    exit
                >
                    <div className="raster raster-static-blue">
                        <img src={Raster} width="100%" height="100%" alt="" ></img>
                    </div>
                </CSSTransition>
            </div>
        );
    }
}

export default WelcomeRects;





