import React from 'react';
import Main from './components/Main';
import './App.css';
import classNames from 'classnames';
import './styles/tablet-animations.scss';
import backgroundVideo from './assets/video/background.mp4';
import ReactPlayer from 'react-player';
import Logo from './assets/image/Kreativa_Europa.png';



class App extends React.Component {
  constructor() {
    super();
    this.state = {
      windowWidth: window.innerWidth,
      windowHeight: window.innerHeight,
      blue: false,
      green: false,
      pink: false,
      welcome: true,
      counter: 0,
      media: 'desktop'
    }
    this.handleClickBlue = this.handleClickBlue.bind(this);
    this.handleClickGreen = this.handleClickGreen.bind(this);
    this.handleClickPink = this.handleClickPink.bind(this);
    
    // this.updateDimensions = this.updateDimensions.bind(this); // not used

    this.greenButton = React.createRef();
    this.pinkButton = React.createRef();
    this.pinkButton = React.createRef();
  }
  componentDidMount() {
    this.handleWindowSizeChange();
    window.addEventListener('resize', this.handleWindowSizeChange);
  }

  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
    const isMobile = this.state.width <= 800;
    const isTablet = (this.state.width <= 1000 && this.state.width >  800);
    const isDesktop = this.state.width > 1000;
    console.log('window width: ', window.innerWidth, ' window height: ', window.innerHeight);
    if(isMobile){
      console.log('is mobile');
      this.setState({
        media: 'mobile',
      })
    }
    if(isDesktop){
      console.log('is browser');
      this.setState({
        media: 'desktop',
      })
    }
    if(isTablet){
      console.log('is tablet');
      this.setState({
        media: 'tablet',
      })
    }
  };

  // componentDidMount() {
  //   this.updateDimensions();
  //   window.addEventListener("resize", this.updateDimensions);
  // }

  // updateDimensions() {
  //   let ww = typeof window !== "undefined" ? window.innerWidth : 0;
  //   let wh = typeof window !== "undefined" ? window.innerHeight : 0;
  //   this.setState({
  //     windowHeight: wh,
  //     windowWidth: ww
  //   });
  // }

  handleClickBlue() {
    console.log('blue clicked!..')
    this.setState({
      blue: true,
      green: false,
      pink: false,
      welcome: false,
    });
    this.setState((prevState, props) => ({
      counter: prevState.counter + 1
    }));
    // get button and add class active
  }

  handleClickGreen() {
    console.log('green clicked!..')
    this.setState({
      blue: false,
      green: true,
      pink: false,
      welcome: false
    });
    this.setState((prevState, props) => ({
      counter: prevState.counter + 1
    }));
  }

  handleClickPink() {
    console.log('pink clicked!..')
    this.setState({
      blue: false,
      green: false,
      pink: true,
      welcome: false
    });
    this.setState((prevState, props) => ({
      counter: prevState.counter + 1
    }));
  }

  // Render buttons and Main with passed down boolean values for colors.
  render() {
    // Window Size 
    //console.log('ww: ', ww.toString(), ' wh: ', wh.toString());
    
    // Buttons 
    var greenButtonClass = classNames({
      btn: true,
      'button-green': true,
      'active': this.state.green,
    });
    var blueButtonClass = classNames({
      btn: true,
      'button-blue': true,
      'active': this.state.blue,
    });
    var pinkButtonClass = classNames({
      btn: true,
      'button-pink': true,
      'active': this.state.pink,
    });

    
    return (
      //playbackRate="0.7" Setting in ReactPlayer to slow down playback rate
      <div >
        <ReactPlayer
          url={[backgroundVideo]}
          // config={
          //   {
          //     attributes: {
          //       style: { height: {wh}, width: {ww} }}
          //   }
          // }
          playing
          loop
          autoPlay
          width="auto"
          height="100%"
          fluid="false"
          className="background-video"
        />

        <nav className="navbar navbar-expand navbar-light bg-transparent">
        <a className="navbar-brand" href="/">
          <img src={Logo} alt="not loading" className="logo-image"></img>
        </a>
          <ul className="navbar-nav ml-auto" ref={this.buttons}>
            <li><button className={blueButtonClass} onClick={this.handleClickBlue} ref={this.blueButton}>Om Kreativa Europa</button></li>
            <li><button className={pinkButtonClass} onClick={this.handleClickPink} ref={this.pinkButton}>Kultur</button></li>
            <li><button className={greenButtonClass} onClick={this.handleClickGreen} ref={this.greenButton}>Media</button></li>
          </ul>
        </nav>
        <Main blue={this.state.blue}
          green={this.state.green}
          pink={this.state.pink}
          welcome={this.state.welcome}
          counter={this.state.counter}
          pinkClick={this.handleClickPink}
          blueClick={this.handleClickBlue}
          greenClick={this.handleClickGreen}
          media={this.state.media}
        ></Main>
      </div>
    );
  }
}

export default App;